.popup__container {
    width: 430px;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0px 0px 25px 0px #00000026;
  
    margin: 195px auto 0 auto;
    position: relative;
  }
  
  @media screen and (max-width: 550px) {
    .popup__profile {
      width: calc(100% - 19px * 2);
      margin: 123px 19px 0 19px;
    }
  }
  